import { Component, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ContactsService } from '../contacts.service';
import {Router} from "@angular/router";
import { EditUserComponent } from '../edit-user/edit-user.component';
import { UserContactsService } from '../user-contacts.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import * as moment from 'moment';
import { ApiService } from '@fuse/services/api.service';

@Component({
    selector     : 'fuse-contacts-contact-list',
    templateUrl  : './contact-list.component.html',
    styleUrls    : ['./contact-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseContactsContactListComponent implements OnInit
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;

    contacts: any;
    user: any;
    dataSource: any;
    displayedColumns = [ 'avatar', 'name', 'email', 'phone', 'status', 'buttons'];
    dialogRef: any;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    userType: string;

    constructor(
        private contactsService: ContactsService,
        public dialog: MatDialog,
        private router: Router,
        private apiService: ApiService,
        private apiloader: ApiLoaderService,
        private toast: ToasterService){}

    ngOnInit(){
        this.userType = sessionStorage.getItem('role');
        this.getAllUsers();
    }    

    openModal(contact)
    {
        this.dialogRef = this.dialog.open(EditUserComponent, {
            panelClass: 'edit-user-dialog',
            data: { 
                contact : contact,
                role    : this.userType,
                action  : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response ){ return; }
                else{
                    this.updateUser(response);
                }
            });
    }



    getAllUsers(){
        const id = sessionStorage.getItem('client_id');
        const role = sessionStorage.getItem('role');
        //alert(role);
        if (role === 'CLIENT_ADMIN'){
            
            this.apiService.getData('users', { 'client_id': id } ).subscribe(response => {
                if (response.data){
                    
                    let data = response.data;
                    let users = [];
                    for (let i = 0; i < data.length; i++){
                        if (data[i].role !== 'CLIENT_ADMIN'){
                            users.push(data[i]);
                        }
                    }
                   //console.log(users);
                    this.dataSource = users;
                }
            }, error => {
                this.toast.showError(error);
            });
        }else{
            this.apiService.getData('users').subscribe(response => {
                if (response.data){
                    this.dataSource = response.data;
                }
            }, error => {
                this.toast.showError(error);
            });
        }
    }

    updateUser(formData){
        this.apiloader.setLoader(true);
        formData.join_date = moment(formData.join_date).format('YYYY-MM-DD');
        formData.birth_date = moment(formData.birth_date).format('YYYY-MM-DD');
        let data = this.apiService.setData(formData);    
        this.apiService.postData('users/' + formData.id, data).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.toast.showSuccess('User Updated Successfully!.');
                this.getAllUsers();
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
      }


      /**
     * Edit Contact
     */
    editContact(contact)
    {
        this.router.navigate(['/apps/user-contacts/edit-employee', contact.id]);
    }

    /**
     * Delete Contact
     */
    deleteContact(contact)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to Delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.apiloader.setLoader(true);
                this.apiService.deleteData('users/' + contact.id).subscribe(response => {
                    if (response.status === 'success'){
                        this.apiloader.setLoader(false);
                        this.getAllUsers();
                        this.toast.showSuccess('User Deleted Successfully!.');
                    }
                }, error => {
                    this.apiloader.setLoader(false);
                    this.toast.showError(error);
                });
            }
            this.confirmDialogRef = null;
        });

    }

    calClick(contact){
        this.router.navigate(['/apps/user-calendar', contact.id]);
    }

}
