import { Component, OnInit } from '@angular/core';
import { EditFormComponent } from './edit-form/edit-form.component';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { UserContactsService } from '../user-contacts.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ActivatedRoute } from '@angular/router';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { Router, NavigationEnd, RoutesRecognized } from '@angular/router';
import { filter, pairwise } from 'rxjs/operators';
import * as moment from 'moment';

@Component({
  selector: 'app-edit-employee',
  templateUrl: './edit-employee.component.html',
  styleUrls: ['./edit-employee.component.scss']
})

export class EditEmployeeComponent implements OnInit {
  
  dialogRef: any;
  user: any;
  userId: string;
  performance: any;
  selectedTab = 0;

  constructor(private router: Router, private userService: UserContactsService, public dialog: MatDialog, private apiloader: ApiLoaderService,
    private route: ActivatedRoute, private toast: ToasterService) {
        this.route.params.subscribe(params => {
            this.userId = params['userId'];
            if (this.userId) {
              this.getUserDetails(this.userId);
            }
        });
        this.router.events
        .pipe(filter((e: any) => e instanceof RoutesRecognized),
            pairwise()
        ).subscribe((e: any) => {
          if(e[0].urlAfterRedirects.indexOf('/performance/employee-performance') != -1) {
            this.selectedTab = 2;
          } else {
            this.selectedTab = 0;
          }
        });
  }

  ngOnInit() {
  }

  setIndex(event) {
    this.selectedTab = event;
  }

  editform(title)
  {
   // console.log(custon);
      this.dialogRef = this.dialog.open(EditFormComponent, {
          panelClass: 'edit-form-dialog',
          data      : { dtitle: title, user: this.user }
      });

      this.dialogRef.afterClosed()
          .subscribe((response: FormGroup) => {
            if ( !response )
            {
                return;
            }else{
                this.updateUser(response);
            }
     });
  }

  getUserDetails(id){
    this.apiloader.setLoader(true);
    this.userService.getData('users/' + id).subscribe(response => {
        if (response.data){
            this.user = response.data;
            this.apiloader.setLoader(false);
        }
    }, error => {
        this.apiloader.setLoader(false);
    });
  }

  updateUser(formData) {
    if(formData.joining_date) {
      formData.joining_date = moment(formData.joining_date).format('YYYY-MM-DD');
    }
    if(formData.birth_date) {
      formData.birth_date = moment(formData.birth_date).format('YYYY-MM-DD');
    }

    this.apiloader.setLoader(true);
    this.userService.postData('users/' + this.userId, formData).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.toast.showSuccess('User Updated Successfully!.');
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

}



  
