import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material';

import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import { fuseAnimations } from '@fuse/animations';

import { FuseContactsContactFormDialogComponent } from './contact-form/contact-form.component';
import { ContactsService } from './contacts.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { UserContactsService } from './user-contacts.service';
import {Router,NavigationEnd, ActivatedRoute} from "@angular/router";
@Component({
    selector     : 'fuse-contacts',
    templateUrl  : './contacts.component.html',
    styleUrls    : ['./contacts.component.scss'],
    animations   : fuseAnimations
    
})
export class FuseContactsComponent implements OnInit, OnDestroy
{
    hasSelectedContacts: boolean;
    searchInput: FormControl;
    dialogRef: any;
    onSelectedContactsChangedSubscription: Subscription;
    userType: string = sessionStorage.getItem('role');
    title: any

    constructor(
        private route: ActivatedRoute,
        private contactsService: ContactsService,
        public dialog: MatDialog,
        private userService: UserContactsService,
        private apiloader: ApiLoaderService,
        private toast: ToasterService,
        private router: Router,
    )
    {
       
        router.events.subscribe((val) => {
           if(val instanceof NavigationEnd){
                if(this.router.url == '/apps/user-contacts'){
                    this.title = 'User Contacts';
                } else {
                    if (this.router.url.indexOf('user-contacts/edit-employee/') != -1 ){
                        this.route.firstChild.params.subscribe(params => {
                            if(params['userId']) {
                                this.getUserDetails(params['userId']);
                            }
                        });
                    }
                }
            }           
        });

        this.searchInput = new FormControl('');
    }

    openModal()
    {
        this.dialogRef = this.dialog.open(FuseContactsContactFormDialogComponent, {
            panelClass: 'contact-form-dialog',
            data      : { }
        });

        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }else{
                    this.inviteUser(response);
                }

            });

    }

    ngOnInit()
    {         
        this.onSelectedContactsChangedSubscription =
            this.contactsService.onSelectedContactsChanged
                .subscribe(selectedContacts => {
                    this.hasSelectedContacts = selectedContacts.length > 0;
                });

        this.searchInput.valueChanges
            .pipe(
                debounceTime(300),
                distinctUntilChanged()
            )
            .subscribe(searchText => {
                this.contactsService.onSearchTextChanged.next(searchText);
            });
    }
    
    inviteUser(formData){
        this.apiloader.setLoader(true);
        const body = new URLSearchParams();
        body.set('email', formData.email);
        body.set('role', formData.role);
        body.set('phone', formData.phone);
        body.set('country_code', formData.country_code);

        this.userService.postData('clients/invite-user', body.toString()).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.toast.showSuccess('User Invited Successfully!');
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

    getUserDetails(id){
      this.userService.getData('users/' + id).subscribe(response => {
          if (response.data){
              this.title = response.data.first_name + ' ' + response.data.last_name;
          }
      });
    }

    ngOnDestroy()
    {
        this.onSelectedContactsChangedSubscription.unsubscribe();
    }
}
