import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { Contact } from '../contact.model';

@Component({
    selector     : 'fuse-contacts-contact-form-dialog',
    templateUrl  : './contact-form.component.html',
    styleUrls    : ['./contact-form.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class FuseContactsContactFormDialogComponent
{
    dialogTitle: string;
    contactForm: FormGroup;    action: string;
    contact: Contact;

    constructor(
        public dialogRef: MatDialogRef<FuseContactsContactFormDialogComponent>,
        @Inject(MAT_DIALOG_DATA) private data: any,
        private formBuilder: FormBuilder
    )
    {
            this.dialogTitle = 'Invite User';
            this.contact = new Contact({});

        this.contactForm = this.createContactForm();
    }

    createContactForm()
    {
        return this.formBuilder.group({
            email          : ['', [Validators.required, Validators.email]],
            phone          : [''],
            role           : [''],
            country_code   : ['']
        });
    }

    inviteUser(formData){
        this.dialogRef.close(formData);
    }
}
