import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';

import {  MatRadioModule,MatListModule,MatTabsModule,MatButtonModule, MatCheckboxModule, MatDatepickerModule,
            MatFormFieldModule, MatSelectModule, MatIconModule, MatSlideToggleModule,
             MatInputModule, MatMenuModule, MatRippleModule, MatSidenavModule, MatTableModule, MatToolbarModule } from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';

import { FuseContactsMainSidenavComponent } from './sidenavs/main/main.component';
import { FuseContactsComponent } from './contacts.component';
import { ContactsService } from './contacts.service';
import { FuseContactsContactListComponent } from './contact-list/contact-list.component';
import { FuseContactsSelectedBarComponent } from './selected-bar/selected-bar.component';
import { FuseContactsContactFormDialogComponent } from './contact-form/contact-form.component';
import { EditUserComponent } from './edit-user/edit-user.component';
import { UserContactsService } from './user-contacts.service';
import { EditEmployeeComponent } from './edit-employee/edit-employee.component' ;
import { EmpCalendarComponent } from './edit-employee/emp-calendar/emp-calendar.component';
import { EmpPerformanceComponent } from './edit-employee/emp-performance/emp-performance.component';
import { CalendarModule } from 'angular-calendar';
import { ColorPickerModule } from 'ngx-color-picker';
import { EmpCalendarService } from './edit-employee/emp-calendar/emp-calendar.service';
import { FuseCalendarEventFormDialogComponent } from './edit-employee/emp-calendar/event-form/event-form.component';
import { ApiCalendarService } from './edit-employee/emp-calendar/api-calendar.service';
import { AnalyticsDashboardService } from './edit-employee/emp-performance/analytics.service';

import { AgmCoreModule } from '@agm/core';
import { ChartsModule } from 'ng2-charts';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { EditFormComponent } from './edit-employee/edit-form/edit-form.component';
import { AttendenceComponent } from './edit-employee/attendence/attendence.component';
import { LeaveManagementComponent } from './edit-employee/leave-management/leave-management.component';
import { EventDialogComponent } from './edit-employee/leave-management/event-dialog/event-dialog.component';
import { LeaveDialogComponent } from './edit-employee/leave-management/leave-dialog/leave-dialog.component';

const routes: Routes = [
    {
        path     : '',
        component: FuseContactsComponent,
        resolve  : {
            contacts: ContactsService
        },
        children:[
            
                {
                    path     : '',
                    component: FuseContactsContactListComponent
                },
                {
                    path     : 'edit-employee/:userId',
                    component: EditEmployeeComponent,
                    resolve  : {
                        data: AnalyticsDashboardService,
                   }
                }

        ]
    }
];

@NgModule({
    declarations   : [
        EmpCalendarComponent,
        EmpPerformanceComponent,
        FuseContactsComponent,
        FuseContactsContactListComponent,
        FuseContactsSelectedBarComponent,
        FuseContactsMainSidenavComponent,
        FuseContactsContactFormDialogComponent,
        EditUserComponent,
        EditEmployeeComponent,
        FuseCalendarEventFormDialogComponent,
        EditFormComponent,
        AttendenceComponent,
        LeaveManagementComponent,
        EventDialogComponent,
        LeaveDialogComponent
    ],
    imports        : [
        RouterModule.forChild(routes),
        MatRadioModule,
        CalendarModule.forRoot(),
        ColorPickerModule,
        CdkTableModule,
        MatTabsModule,
        MatButtonModule,
        MatCheckboxModule,
        MatDatepickerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatSelectModule,
        MatMenuModule,
        MatSlideToggleModule,
        MatRippleModule,
        MatSidenavModule,
        MatTableModule,
        MatToolbarModule,
        MatListModule,
        FuseSharedModule,
        FuseConfirmDialogModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyD81ecsCj4yYpcXSLFcYU97PvRsE_X8Bx8'
        }),
        ChartsModule,
        NgxChartsModule,
    ],
    providers      : [
        ContactsService,
        UserContactsService,
        EmpCalendarService,
        ApiCalendarService,
        AnalyticsDashboardService
       
    ],
    entryComponents: [LeaveDialogComponent, EventDialogComponent, FuseContactsContactFormDialogComponent,FuseCalendarEventFormDialogComponent, EditUserComponent,EditFormComponent]
})
export class UserContactsModule
{
}
