import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Subject } from 'rxjs';
import { startOfDay, isSameDay, isSameMonth } from 'date-fns';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarMonthViewDay } from 'angular-calendar';

import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { fuseAnimations } from '@fuse/animations';

import { EventDialogComponent } from './event-dialog/event-dialog.component';
import { CalEventModel } from './cal-event.model';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { meetings } from '@fuse/constant'
import { HttpClient } from '@angular/common/http';
import { UserContactsService } from '../../user-contacts.service';
import { LeaveDialogComponent } from './leave-dialog/leave-dialog.component';

@Component({
  selector: 'app-leave-management',
  templateUrl: './leave-management.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./leave-management.component.scss']
})
export class LeaveManagementComponent implements OnInit {
  view: string;
  viewDate: Date;
  events: CalendarEvent[];
  public actions: CalendarEventAction[];
  activeDayIsOpen: boolean;
  refresh: Subject<any> = new Subject();
  dialogRef: any;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  selectedDay: any;
  task: any;
  userId: any;
  meeting: any;
  months:any;
  years = [];
  selectm:any;
  selecty:any;

  constructor(
      private http: HttpClient,
      public dialog: MatDialog,
      private calService: UserContactsService,
      private apiloader: ApiLoaderService,
      private toast: ToasterService,
      private route: ActivatedRoute
  )
  {
      this.view = 'month';
      this.viewDate = new Date();
      this.activeDayIsOpen = false;
      this.selectedDay = {date: startOfDay(new Date())};

      if (sessionStorage.getItem('role') === 'CLIENT_ADMIN'){
          this.actions = [
              {
                  label  : '<i class="material-icons s-16">edit</i>',
                  onClick: ({event}: { event: CalendarEvent }): void => {
                      this.editEvent('edit', event);
                  }
              },
              {
                  label  : '<i class="material-icons s-16">delete</i>',
                  onClick: ({event}: { event: CalendarEvent }): void => {
                      this.deleteEvent(event);
                  }
              }
          ];
      } else { this.actions = []; }

      //---- DropDown for month and year -----//
      let date = new Date();
      let currentYear = date.getFullYear();
  
      //set values for year dropdown
      for (var i = 0; i <= 100; i++) {
        this.years.push(currentYear + i);
      }

      this.months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      this.selectm = this.months[this.viewDate.getMonth()];
      this.selecty = currentYear;
  }

  ngOnInit() {
        this.http.get('api/calendar-client/events')
            .subscribe((response: any) => {
                this.events = response.data;
                this.setEvents();
        });
        this.route.params.subscribe(params => {
          this.userId = params['id'];
      });
  }

  onchange(event){
    this.selectm=event;
    let d=this.selecty+"-"+this.monthNameToNum(event)+"-01 00:00:00";
    this.viewDate = new Date(d);
   
  }

  onchangey(event){
      this.selecty=event;
      let d=event+"-"+this.monthNameToNum(this.selectm)+"-01 00:00:00";
      this.viewDate = new Date(d);
    
  }
   monthNameToNum(monthname) {
    var month = this.months.indexOf(monthname);
    return month ? month + 1 : 0;
   }

  setEvents()
  {
      this.events = this.events.map(item => {
          return new CalEventModel(item);
      });
  }

  beforeMonthViewRender({header, body})
  {        
      body.forEach((day) => {
          
      });
  }

    dayClicked(day: CalendarMonthViewDay): void
    {
          const date: Date = day.date;
          const events: CalendarEvent[] = day.events;

          if ( isSameMonth(date, this.viewDate) )
          {
              if ( (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) || events.length === 0 )
              {
                  this.activeDayIsOpen = false;
              }
              else
              {
                  this.activeDayIsOpen = true;
                  this.viewDate = date;
              }
          }
          this.selectedDay = day;
          this.refresh.next();
    }

    eventTimesChanged({event, newStart, newEnd}: CalendarEventTimesChangedEvent): void
    {
        event.start = newStart;
        event.end = newEnd;
        // console.warn('Dropped or resized', event);
        this.refresh.next(true);
    }

    /**
     * Delete Event
     * @param event
     */
    deleteEvent(event)
    {
        console.log(event);
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                const eventIndex = this.events.indexOf(event);
                this.events.splice(eventIndex, 1);
                this.refresh.next(true);
            }
            this.confirmDialogRef = null;
        });

    }

    /**
     * Edit Event
     * @param {string} action
     * @param {CalendarEvent} event
     */
    editEvent(action: string, event: CalendarEvent)
    {
        const eventIndex = this.events.indexOf(event);

        this.dialogRef = this.dialog.open(EventDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                event : event,
                action: action
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.events[eventIndex] = Object.assign(this.events[eventIndex], formData.getRawValue());
                        this.refresh.next(true);

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteEvent(event);

                        break;
                }
            });
    }

    /**
     * Add Event
     */
    addEvent(): void
    {
        this.dialogRef = this.dialog.open(EventDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                action: 'new',
                date  : this.selectedDay.date
            }
        });
        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }
                const newEvent = response.getRawValue();
                newEvent.actions = this.actions;
                this.events.push(newEvent);
                this.refresh.next(true);
            });
    }

    openLeave() {
      this.dialogRef = this.dialog.open(LeaveDialogComponent, {
          panelClass: 'leave-form-dialog',
          data      : {
              event : event,
              action: 'details'
          }
      });
    }

}
