import { Component, OnInit, ViewEncapsulation,ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { AnalyticsDashboardService } from './analytics.service';
import { fuseAnimations } from '@fuse/animations';
import { ContactsFakeDb } from '../../../../../../fuse-fake-db/contacts';
import { Subject } from 'rxjs';
// import { startOfDay, isSameDay, isSameMonth } from 'date-fns';
// import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarMonthViewDay } from 'angular-calendar';
// import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table'

import { ToasterService } from '@fuse/services/toaster-service.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ActivatedRoute } from '@angular/router';
import { UserContactsService } from '../../user-contacts.service';
// import { AnalyticsDashService } from './analytics-dash.service';

export interface PeriodicElement {
  performance_forms: string;
  position: number;
  status: string;
  overall_rating: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, performance_forms: 'Hydrogen', status: 'Demo', overall_rating: 'H'},
  {position: 2, performance_forms: 'Helium', status: 'Demo', overall_rating: 'He'},
  {position: 3, performance_forms: 'Lithium', status: 'Demo', overall_rating: 'Li'},
  {position: 4, performance_forms: 'Beryllium', status: 'Test', overall_rating: 'Be'},
  {position: 5, performance_forms: 'Boron', status: 'Test', overall_rating: 'B'},
  {position: 6, performance_forms: 'Carbon', status: 'Test', overall_rating: 'C'},
  {position: 7, performance_forms: 'Nitrogen', status: 'Test', overall_rating: 'N'},
  {position: 8, performance_forms: 'Oxygen', status: 'Test', overall_rating: 'O'},
  {position: 9, performance_forms: 'Fluorine', status: 'Test', overall_rating: 'F'},
  {position: 10, performance_forms: 'Neon', status: 'Test', overall_rating: 'Ne'},
];


@Component({
  selector: 'app-emp-performance',
  templateUrl: './emp-performance.component.html',
  styleUrls: ['./emp-performance.component.scss'],
  animations   : fuseAnimations
})
export class EmpPerformanceComponent implements OnInit {
 

    widgets: any;
    widget1SelectedYear = '2016';
    widget5SelectedDay = 'today';
    searchInput: FormControl;
    contacts: any = ContactsFakeDb.contacts;
    displayedColumns = [ 'avatar', 'name', 'email', 'phone'];
    dataSource: any;
    client: any;
    task: any;
    id;
    performance;
    
    viewDate: Date;
    selectm:any;
    selecty:any;
    months:any;
    years = [];

    displayedColumns1: string[] = ['position', 'performance_forms', 'status', 'overall_rating','buttons'];
    dataSource1 = ELEMENT_DATA;

  constructor(private userService: UserContactsService, private analyticsDashboardService: AnalyticsDashboardService,private route: ActivatedRoute,
    public dialog: MatDialog,
    private apiloader: ApiLoaderService,
    private toast: ToasterService,
    // private analyics: AnalyticsDashService
    ) { 
      this.route.params.subscribe( params => {
        this.id = params['userId'];
        this.getPerformance(this.id);
      });
      // Get the widgets from the service
      this.widgets = this.analyticsDashboardService.widgets;
       //console.log(this.widgets);
      // Register the custom chart.js plugin
      this.registerCustomChartJSPlugin();

      //---- DropDown for month and year -----//
      var date = new Date();
      var currentYear = date.getFullYear();
  
      //set values for year dropdown
      for (var i = 0; i <= 100; i++) {
        this.years.push(currentYear + i);
      }
  
      //set values for month dropdown
      this.months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      // this.selectm = this.months[this.viewDate.getMonth()];
      // this.selecty = currentYear;
    }
  
   
  
   
  ngOnInit() {
  }
  


  onchange(event){
    //console.log(event);
    this.selectm=event;
    let d=this.selecty+"-"+this.monthNameToNum(event)+"-01 00:00:00";
    this.viewDate = new Date(d);
   
  }

  getPerformance(id){
    this.apiloader.setLoader(true);
    this.userService.getData('performance/' + id).subscribe(response => {
        if (response.data){
            this.performance = response.data;
            this.apiloader.setLoader(false);
        }
    }, error => {
        this.apiloader.setLoader(false);
    });
  }

  onchangey(event){
      this.selecty=event;
      let d=event+"-"+this.monthNameToNum(this.selectm)+"-01 00:00:00";
      this.viewDate = new Date(d);
    
  }
   monthNameToNum(monthname) {
    var month = this.months.indexOf(monthname);
    return month ? month + 1 : 0;
   }

   /**
     * Register a custom plugin
     */
    registerCustomChartJSPlugin()
    {
        (<any>window).Chart.plugins.register({
            afterDatasetsDraw: function (chart, easing) {
                // Only activate the plugin if it's made available
                // in the options
                if (
                    !chart.options.plugins.xLabelsOnTop ||
                    (chart.options.plugins.xLabelsOnTop && chart.options.plugins.xLabelsOnTop.active === false)
                )
                {
                    return;
                }

                // To only draw at the end of animation, check for easing === 1
                const ctx = chart.ctx;

                chart.data.datasets.forEach(function (dataset, i) {
                    const meta = chart.getDatasetMeta(i);
                    if ( !meta.hidden )
                    {
                        meta.data.forEach(function (element, index) {

                            // Draw the text in black, with the specified font
                            ctx.fillStyle = 'rgba(255, 255, 255, 0.7)';
                            const fontSize = 13;
                            const fontStyle = 'normal';
                            const fontFamily = 'Roboto, Helvetica Neue, Arial';
                            ctx.font = (<any>window).Chart.helpers.fontString(fontSize, fontStyle, fontFamily);

                            // Just naively convert to string for now
                            const dataString = dataset.data[index].toString() + 'k';

                            // Make sure alignment settings are correct
                            ctx.textAlign = 'center';
                            ctx.textBaseline = 'middle';
                            const padding = 15;
                            const startY = 24;
                            const position = element.tooltipPosition();
                            ctx.fillText(dataString, position.x, startY);

                            ctx.save();

                            ctx.beginPath();
                            ctx.setLineDash([5, 3]);
                            ctx.moveTo(position.x, startY + padding);
                            ctx.lineTo(position.x, position.y - padding);
                            ctx.strokeStyle = 'rgba(255,255,255,0.12)';
                            ctx.stroke();

                            ctx.restore();
                        });
                    }
                });
            }
        });
    }

}

