import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

import { MatColors } from '@fuse/mat-colors';
import * as moment from 'moment';
import { CalendarEventModel } from '../event.model';
import { ApiCalendarService } from '../api-calendar.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

@Component({
    selector     : 'fuse-calendar-event-form-dialog',
    templateUrl  : './event-form.component.html',
    styleUrls    : ['./event-form.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class FuseCalendarEventFormDialogComponent
{
    event;
    dialogTitle: string;
    eventForm: FormGroup;
    action: string;
    presetColors = MatColors.presets;
    users: Array<any> = [];
    priority: any;

    radioBtns = [
        {class: 'pink' },
        {class: 'purple'},
        {class: 'green'}
    ];

    constructor(
        public dialogRef: MatDialogRef<FuseCalendarEventFormDialogComponent>,
        @Inject(MAT_DIALOG_DATA) private data: any,
        private formBuilder: FormBuilder,
        private calService: ApiCalendarService,
        private toast: ToasterService
    )
    {
        this.event = data.event;
        this.event.start = moment(this.event.start).format('MMM DD, YYYY');
        this.event.end = moment(this.event.end).format('MMM DD, YYYY');
        this.action = data.action;
        this.getAllUsers();

        if ( this.action === 'edit' )
        {
            this.dialogTitle = 'update Task';
        }
        else
        {
            this.dialogTitle = 'New Event';
            this.event = new CalendarEventModel({
                start: data.date,
                end  : data.date
            });
        }

        this.eventForm = this.createEventForm();
    }

    createEventForm()
    {
        return new FormGroup({
            title : new FormControl(this.event.title, Validators.required),
            description : new FormControl(this.event.description),
            scope : new FormControl(this.event.scope, Validators.required),
            members : new FormControl(this.event.members),
        });
    }

    getAllUsers(){
        const cli_id = sessionStorage.getItem('client_id');
        this.calService.getData('users', { 'client_id': cli_id }).subscribe(response => {
            if (response.data){
                const data = response.data;
                for (let i = 0; i < data.length; i++){
                    if (data[i].role !== 'CLIENT_ADMIN'){
                        this.users.push(data[i]);
                    }
                }
            }
        }, error => {
            this.toast.showError(error);
        });
    }

    closeDialog(type, data){
        data.id = this.event.id;
        this.dialogRef.close({data: data, type: type});
    }
}
