import { Component, OnInit } from '@angular/core';
import { UserContactsService } from '../../user-contacts.service';
import { ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import { ApiLoaderService } from '@fuse/services/api-loader.service';

@Component({
  selector: 'app-attendance',
  templateUrl: './attendence.component.html',
  styleUrls: ['./attendence.component.scss']
})
export class AttendenceComponent implements OnInit {

  viewDate: Date;
  months:any;
  years = [];
  selectm:any;
  selecty:any;
  userId: string;
  attendenceDetails: Array<any>;

  constructor(private userService: UserContactsService, private route: ActivatedRoute, private apiloader: ApiLoaderService) { 

    this.route.params.subscribe( params => {
      this.userId = params['userId'];
    });

    this.viewDate = new Date();
    //---- DropDown for month and year -----//
    let date = new Date();
    let currentYear = date.getFullYear();

    //set values for year dropdown
    for (var i = 0; i <= 100; i++) {
      this.years.push(currentYear + i);
    }

    //set values for month dropdown
    this.months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    this.selectm = this.viewDate.getMonth() + 1;
    this.selecty = currentYear;
  }

  ngOnInit() {
    this.getAttendenceData();
  }

  getAttendenceData() {
    this.apiloader.setLoader(true);
    const body = new URLSearchParams();
    let month = JSON.stringify(this.selectm).length > 1 ? this.selectm : '0' + this.selectm;
    body.set('date', this.selecty + '-' + month + '-' + '01');

    this.userService.postData('attendance/attPerformance/' + this.userId, body.toString()).subscribe(response => {
      if(response) {
        this.apiloader.setLoader(false);
        this.attendenceDetails = response.data;
      }
    }, error => {
      this.apiloader.setLoader(false);
    });
  }

  onChngMonth(event){
    this.selectm = event;
    this.getAttendenceData();
  }

  onChngYear(event){
      this.selecty=event;
      this.getAttendenceData();
  }

}
