import { Component, OnInit,ViewEncapsulation } from '@angular/core';
import { ApiCalendarService } from './api-calendar.service';
import { FormGroup,FormBuilder } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Subject } from 'rxjs';
import { startOfDay, isSameDay, isSameMonth } from 'date-fns';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarMonthViewDay } from 'angular-calendar';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { fuseAnimations } from '@fuse/animations';
import { FuseCalendarEventFormDialogComponent } from './event-form/event-form.component';
import { CalendarEventModel } from './event.model';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import * as moment from 'moment';
import { EmpCalendarService } from './emp-calendar.service';
import { HttpClient } from '@angular/common/http';


interface MyEvent extends CalendarEvent {
    isEvent: boolean;
}

@Component({
  selector: 'app-emp-calendar',
  templateUrl: './emp-calendar.component.html',
  styleUrls: ['./emp-calendar.component.scss'],
  animations   : fuseAnimations
})
export class EmpCalendarComponent implements OnInit {

  view: string;
  viewDate: Date;
  events: CalendarEvent[];
  public actions: CalendarEventAction[];
  activeDayIsOpen: boolean;
  refresh: Subject<any> = new Subject();
  dialogRef: any;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  selectedDay: any;
  task: any;
  months:any;
  years = [];
  selectm:any;
  selecty:any;

  constructor(
      private http: HttpClient,
      private fb: FormBuilder,
      public dialog: MatDialog,
      public calendarService: EmpCalendarService,
      private apiloader: ApiLoaderService,
      private toast: ToasterService,
      private calService: ApiCalendarService
  ){

 
   
    this.view = 'month';
    this.viewDate = new Date();
   
    this.activeDayIsOpen = true;
    this.selectedDay = {date: startOfDay(new Date())};
    
    

    //console.log(this.countryForm.get('month1'));

    this.actions = [
        {
            label  : '<i class="material-icons s-16">edit</i>',
            onClick: ({event}: { event: MyEvent }): void => {
                this.editEvent('edit', event);
            }
        },
        {
            label  : '<i class="material-icons s-16">delete</i>',
            onClick: ({event}: { event: MyEvent }): void => {
                this.deleteEvent(event);
            }
        }
    ];
    

    //---- DropDown for month and year -----//
      let date = new Date();
      let currentYear = date.getFullYear();
  
      //set values for year dropdown
      for (var i = 0; i <= 100; i++) {
        this.years.push(currentYear + i);
      }
  
      //set values for month dropdown
      this.months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      this.selectm = this.months[this.viewDate.getMonth()];
      this.selecty = currentYear;
  }

  ngOnInit() {

    this.http.get('api/calendar-client/events')
        .subscribe((response: any) => {
            this.events = response.data;
            for(let item of this.events) {
            item.actions = this.actions;
            }
            this.setEvents();
    });

    // this.getHolidays();
        
        /**
         * Watch re-render-refresh for updating db
         */
        this.refresh.subscribe(updateDB => {
            // console.warn('REFRESH');
            if ( updateDB )
            {
                // console.warn('UPDATE DB');
                this.calendarService.updateEvents(this.events);
            }
        });

        this.calendarService.onEventsUpdated.subscribe(events => {
            this.setEvents();
            this.refresh.next();
        });
  }  

  onchange(event){
    console.log(event);
    this.selectm=event;
    let d=this.selecty+"-"+this.monthNameToNum(event)+"-01 00:00:00";
    this.viewDate = new Date(d);
   
  }

  onchangey(event){
      this.selecty=event;
      let d=event+"-"+this.monthNameToNum(this.selectm)+"-01 00:00:00";
      this.viewDate = new Date(d);
    
  }
   monthNameToNum(monthname) {
    var month = this.months.indexOf(monthname);
    return month ? month + 1 : 0;
   }

  


  setEvents()
  {
      this.events = this.events.map(item => {
          return new CalendarEventModel(item);
      });
  }
  

  getHolidays(){
    this.apiloader.setLoader(true);
    this.calService.getData('holidays').subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
             const events = response.data;
             for (let i = 0; i < events.length; i++){
                events[i].title = events[i].details;
                events[i].start = new Date(events[i].date);
                events[i].color = {
                    primary  : '#ad2121',
                    secondary: '#FAE3E3'
                };
                events[i].actions = [];
                events[i].isHoliday = true;
             }
             this.events = events;
             this.getTasks();
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
}

getTasks(){
    let id = sessionStorage.getItem('user_id');
    this.apiloader.setLoader(true);
    this.calService.getData('users/' + id).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.task = response.data.tasks;
            for (let i = 0; i < this.task.length; i++){
                this.task[i].color = {
                    primary  : '#ad2121',
                    secondary: '#FAE3E3'
                };
                this.task[i].actions = this.actions;
                this.task[i].start = new Date(this.task[i].start);
                this.events.push(this.task[i]);
            }
            this.setEvents();
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
}

/**
 * Before View Renderer
 * @param {any} header
 * @param {any} body
 */
beforeMonthViewRender({header, body})
{
    body.forEach((day) => {
        const event = day.events;
        if (event.length){
            event.forEach(item => {
                if ( item.isHoliday ){
                    day.cssClass = 'holiday';
                }
            });
        }
    });
}

/**
 * Day clicked
 * @param {MonthViewDay} day
 */
dayClicked(day: CalendarMonthViewDay): void
{
    const date: Date = day.date;
    const events: CalendarEvent[] = day.events;

    if ( isSameMonth(date, this.viewDate) )
    {
        if ( (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) || events.length === 0 )
        {
            this.activeDayIsOpen = false;
        }
        else
        {
            this.activeDayIsOpen = true;
            this.viewDate = date;
        }
    }
    this.selectedDay = day;
    this.refresh.next();
}

/**
 * Event times changed
 * Event dropped or resized
 * @param {CalendarEvent} event
 * @param {Date} newStart
 * @param {Date} newEnd
 */
eventTimesChanged({event, newStart, newEnd}: CalendarEventTimesChangedEvent): void
{
    event.start = newStart;
    event.end = newEnd;
    // console.warn('Dropped or resized', event);
    this.refresh.next(true);
}

/**
 * Delete Event
 * @param event
 */
deleteEvent(event)
{
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
        disableClose: false
    });

    this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

    this.confirmDialogRef.afterClosed().subscribe(result => {
        if ( result )
        {
            this.apiloader.setLoader(true);
            this.calService.deleteData('tasks/' + event.id).subscribe(response => {
                if (response.status === 'success'){
                    this.apiloader.setLoader(false);
                    this.getHolidays();
                    this.toast.showSuccess('Task Deleted Successfully!.');
                }
            }, error => {
                this.apiloader.setLoader(false);
                this.toast.showError(error);
            });
        }
        this.confirmDialogRef = null;
    });

}

/**
 * Edit Event
 * @param {string} action
 * @param {CalendarEvent} event
 */
editEvent(action: string, event: MyEvent)
{        
    this.dialogRef = this.dialog.open(FuseCalendarEventFormDialogComponent, {
        panelClass: 'event-form-dialog',
        data      : {
            event : event,
            action: action
        }
    });

    this.dialogRef.afterClosed()
        .subscribe(response => {
            if ( !response )
            {
                return;
            }
            if (response.type === 'edit'){
                this.updteTask(response.data);
            }
        });
}

    /**
     * Add Event
     */
    addHoliday(): void
    {
        this.dialogRef = this.dialog.open(FuseCalendarEventFormDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                action: 'new',
                date  : this.selectedDay.date
            }
        });
        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }else{
                    this.createHoliday(response);
                }
                
            });
    }

    createHoliday(formData){
        this.apiloader.setLoader(true);
        const body = new URLSearchParams();
        body.set('details', formData.details);
        body.set('date', formData.date);

        this.calService.postData('holidays', body.toString()).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.getHolidays();
                this.toast.showSuccess('Holiday Created Successfully!.');
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

updteTask(formData){
    const edate = moment(formData.end).format('YYYY-MM-DD');
    this.apiloader.setLoader(true);
    const body = new URLSearchParams();
    body.set('title', formData.title);
    body.set('description', formData.description);
    body.set('scope', formData.scope);
    body.set('members', formData.members);
    body.set('end', edate);

    this.calService.postData('tasks/' + formData.id, body.toString()).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.toast.showSuccess('Task Updated Successfully!');
            this.getHolidays();
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
}


}
