import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-leave-dialog',
  templateUrl: './leave-dialog.component.html',
  styleUrls: ['./leave-dialog.component.scss']
})

export class LeaveDialogComponent {
  leaveForm: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<LeaveDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.leaveForm = this.createForm();
  }

  createForm()
    {
        return new FormGroup({
            leaveType : new FormControl('Sick Leave'),
            leaveComment : new FormControl(),
            status : new FormControl(),
            ManageComment : new FormControl()
        });
    }

}
